.preview {
    position: relative;
    text-align: center;
    border-radius: 40px;
    border: none;
    background: none;
    padding: 0;
}

.preview:focus {
    outline:none;
}

.projectImg {
    width: 200px;
    height: 200px;
    border-radius: 40px;
}

.overlay {
    position: absolute;
    border-radius: 40px;
    top: 0;
    left: 0;
    width:100%;
    height:100%;
    background-color:#000;
    visibility: hidden;
    opacity: 0;
    transition: opacity .5s, visibility .5s;
}

.preview:hover .overlay {
    visibility: visible;
    opacity: 0.7;
    z-index: 1;
    display: block;
}

.centered {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    visibility: hidden;
    opacity: 0;
    transition: opacity .5s, visibility .5s;
    color: white;
    z-index: 2;
    font-size: 30px;
}

.isSelected {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    background-color:rgb(255, 255, 255);
}

.preview:hover .centered {
    visibility: visible;
    opacity: 1;
}
